import React from "react";
import ChampionIcon from "./champion-icon";
import "./team-composition.css" 

class TeamCompositionView extends React.Component{
  render(){
    const champions = this.props.ids.map((champ) =>
      <li><ChampionIcon id={champ}/></li>
    );
    return(
      <div>
        <ul class="comp">{champions}</ul>
      </div>
    )
  }
}

class TeamCompositionTable extends React.Component{
  render(){
    const risuto = this.props.risuto;
    const comps = risuto.map((row) =>
      <li><TeamCompositionView ids={row}/></li>
    );
    return(
      <div>
        <ul>{comps}</ul>
      </div>
    )
  }
}

export default TeamCompositionTable;