import React from "react"
import { Link } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"

import TeamCompMemoTool from "../components/tft/tft-comp-memo-tool"

const TFTCompMemoPage = () => (
  <Layout>
    <SEO title="TFTの構成をメモするためのツール" />
    <h1>TFTの構成がメモ出来るやつ</h1>
    <p><a href="https://lolchess.gg/simulator/4.0" target="blank">TFT シナジーシミュレータ</a>のShareボタンから取得できるURLを貼ると構成が表示されます。</p>
    <p>まだまだ開発中</p>
    <TeamCompMemoTool />
  </Layout>
)

export default TFTCompMemoPage
