import React from "react";
import InputCompFromUrl from "./input-comp-from-url";
import TeamCompositionTable from "./team-composition";

class TFTCompMemoTool extends React.Component{
  constructor(props){
    super();
    this.storage = typeof localStorage !== "undefined" ? localStorage: NaN;
    this.state = {
      risuto: this.importCompsFromLocalStorage()
    }
  }

  // ローカルストレージに保存されている構成を読み込む
  importCompsFromLocalStorage(){
    let comps = [];
    for (let i=0; i<this.storage.length; i++){
      var key = this.storage.key(i);
      comps.push(this.storage.getItem(key).split(","))
    }
    return comps;
  }

  updateState(state){
    this.setState(state);
    for (let i=0; i<state.risuto.length; i++){
      this.storage.setItem(i, state.risuto[i].join())
    }
  }

  clearComps(){
    this.setState({risuto: []})
    this.storage.clear()
  }

  render(){
    return(
			<div>
        <InputCompFromUrl risuto={this.state.risuto} updateState={this.updateState.bind(this)}/>
        <input type="button" value="リセット" onClick={() => this.clearComps()}/>
      	<TeamCompositionTable risuto={this.state.risuto} />
			</div>
    )
  }
}

export default TFTCompMemoTool;